body {
  font-family: 'Russo One';
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.ml-4 {
  margin-left: 4px;
}

.active {
  background-color: orange;
  box-shadow: 0 3px orange;
  height: 77;
  margin-top: 13;
}

#drum-machine {
  outline: 5px solid orange;
  position: relative;
  width: 660px;
  text-align: center;
  background-color: #b3b3b3;
}

.pad-bank {
  width: 332px;
  height: 272px;
  display: inline-block;
  margin: 20px;
}

.drum-pad {
  background-color: grey;
  margin-top: 10px;
  box-shadow: black 3px 3px 5px;
  position: relative;
  float: left;
  width: 100px;
  height: 80px;
  margin-right: 10px;
  border-radius: 5px;
  padding-top: 35px;
  box-sizing: border-box;
  cursor: pointer;
}

.logo {
  position: absolute;
  top: 5px;
  right: 10px;
}

.logo .inner-logo {
  font-size: 20px;
  font-weight: bold;
  font-style: italic;
  display: inline-block;
}

.controls-container {
  width: 240px;
  height: 272px;
  display: inline-block;
  margin: 40px 20px 0 10px;
  vertical-align: top;
}

.controls-container #control {
  width: 100px;
  margin: auto;
}

#control p {
  margin-bottom: 0;
}

#control .select {
  margin: auto;
  border: 1px solid black;
  width: 50px;
  height: 20px;
  padding: 1px;
  background-color: black;
}

#control .select .select-inner {
  width: 23px;
  height: 19px;
  background-color: blue;
  border: 1px solid black;
  box-sizing: border-box;
  cursor: pointer;
  float: right;
}

.controls-container #display {
  width: 200px;
  background-color: gray;
  margin: 15px auto;
  padding: 15px;
  box-sizing: border-box;
}

.controls-container #volume-slider input {
  /* -webkit-appearance: none; */
  margin: 12.5px 0;
  width: 100%;
}